export let keys: string[] = [];

export const setKey = (key: string) => {
  keys = [];
  {
    [...Array(26)].map((v, i: number) => {
      keys.push(
        `${String.fromCharCode(65 + i)}${key}${String.fromCharCode(
          65 - i + key.length
        )}`
      );
      keys.push(
        `${String.fromCharCode(
          65 + i
        ).toLowerCase()}${key}${String.fromCharCode(65 - i + key.length)}`
      );
    });
  }
};

export const getKeys = () => {
  return keys;
};
