import { encrypt as e, decrypt as d } from "../../include/encryption";
import { getKeys } from "../../include/key";

function useEncryption() {
  const encrypt = async (text: string): Promise<string> => {
    return await e(text, getKeys().length > 0 ? getKeys() : ["x"]);
  };

  const decrypt = async (text: string): Promise<string> => {
    return await d(text, getKeys().length > 0 ? getKeys() : ["x"]);
  };

  return { encrypt, decrypt };
}
export default useEncryption;
