// Encypt String Value
export const encryptString = async (
  text: string,
  fixed: boolean
): Promise<string> => {
  const shift = fixed ? 10 : text.length;
  let encrypted = "";

  for (let i = 0; i < text.length; i++) {
    const charCode = text.charCodeAt(i);

    if (charCode >= 65 && charCode <= 90) {
      encrypted += String.fromCharCode(((charCode - 65 + shift) % 26) + 65);
    } else if (charCode >= 97 && charCode <= 122) {
      encrypted += String.fromCharCode(((charCode - 97 + shift) % 26) + 97);
    } else {
      encrypted += text[i];
    }
  }

  return encrypted;
};

// Convert to HEX
export const to = async (text: string): Promise<string> => {
  let hexString = "";

  for (let i = 0; i < text.length; i++) {
    const charCode = text.charCodeAt(i);
    hexString += ("0" + charCode.toString(16)).slice(-2);
  }

  return hexString;
};

// Decrypt String Value
export const decryptString = async (
  text: string,
  fixed: boolean
): Promise<string> => {
  const shift = fixed ? 10 : text.length;
  let decrypted = "";

  for (let i = 0; i < text.length; i++) {
    const charCode = text.charCodeAt(i);
    decrypted += decryptCase(text[i], charCode, shift);
  }

  return decrypted;
};

// Decrypt case
const decryptCase = (char: string, charCode: number, shift: number): string => {
  let charValue = "";
  if (charCode >= 65 && charCode <= 90) {
    charValue = String.fromCharCode(((charCode - 65 - shift + 26) % 26) + 65);
  } else if (charCode >= 97 && charCode <= 122) {
    charValue = String.fromCharCode(((charCode - 97 - shift + 26) % 26) + 97);
  } else {
    return char;
  }
  return charValue.charCodeAt(0) >= 65 && charValue.charCodeAt(0) <= 90
    ? charValue.toUpperCase()
    : charValue;
};

// Convert from HEX
export const from = async (hex: string, fixed: boolean): Promise<string> => {
  let decrypted = "";

  for (let i = 0; i < hex.length; i += 2) {
    const charCode = parseInt(hex.substr(i, 2), 16);
    decrypted += String.fromCharCode(charCode);
  }

  return await decryptString(decrypted, fixed);
};

const randomKey = (secrets: string[]) => {
  const rand = Math.floor(Math.random() * secrets.length);
  return secrets[rand];
};

const randomPlace = (secret: string, text: string) => {
  var pos = Math.floor(Math.random() * (text.length + 1));
  return text.substring(0, pos) + secret + text.substring(pos);
};

export const encrypt = async (
  value: string,
  secrets: string[] = ["x"],
  fixed: boolean = true
): Promise<string> => {
  const encrypted = await encryptString(
    randomPlace(randomKey(secrets), value),
    fixed
  );

  return await encryptString(await to(encrypted), fixed);
};

export const decrypt = async (
  value: string,
  secrets: string[] = ["x"],
  fixed: boolean = true
): Promise<string> => {
  const d = await from(await decryptString(value, fixed), fixed);

  let decryptedText: any = null;
  if (d && secrets.length > 0) {
    secrets.forEach((secret) => {
      if (d.includes(secret)) {
        decryptedText = d.replace(secret, "");
      }
    });
  }
  if (decryptedText) return decryptedText;
  return "ERROR: Cannot be decrypted!";
};
