import { encrypt as e, decrypt as d } from "./encryption";
import { setKey, getKeys } from "./key";

export const secret = (key: string) => {
  setKey(key);
};

export const encrypt = async (text: string): Promise<string> => {
  return await e(text, getKeys().length > 0 ? getKeys() : ["x"]);
};

export const decrypt = async (text: string): Promise<string> => {
  return await d(text, getKeys().length > 0 ? getKeys() : ["x"]);
};
